:root {
  --main-color: #72b626;
}

header .top-bar nav ul {
  right: -100%;
  left: auto;
}

header .top-bar nav ul.show-nav {
  right: 0;
  left: auto;
}

@media (min-width: 992px) {
  header .top-bar nav ul li a:hover span {
    margin-right: 0;
    margin-left: 10px;
  }
}

@media (min-width: 992px) {
  header .top-bar nav ul {
    right: auto;
    left: 2%;
  }
}

header .top-bar .mobile-btn {
  left: 5%;
  right: auto;
}

header .color-setting {
  left: auto;
  right: -244px;
}

header .color-setting.show-color-setting {
  right: 0;
  left: auto;
}

header .color-setting .color-setting-content .color-setting-dark-colors .color-setting-dark-color:nth-of-type(2) {
  padding: 3px 2px 2px 27px;
}

header .color-setting .color-setting-content .color-setting-dark-colors .color-setting-dark-color:nth-of-type(2).dark-mode {
  padding: 3px 27px 2px 2px;
}

header .color-setting .color-setting-content .color-setting-language-colors .color-setting-language-color {
  order: 1;
}

header .color-setting .color-setting-content .color-setting-language-colors .color-setting-language-color a span {
  margin-left: 0;
  margin-right: 10px;
}

header .color-setting .color-setting-content .color-setting-language-colors .color-setting-language-color:first-of-type {
  order: 2;
}

header .color-setting .color-setting-content .color-setting-language-colors .color-setting-language-color .disable-language span {
  margin-left: 0;
  margin-right: 10px;
}

header .color-setting .color-setting-icon {
  box-shadow: -4px 0px 4px 0px rgba(51, 51, 51, 0.2);
  border-radius: 5px 0 0 5px;
}

@media (min-width: 992px) {
  .banner .banner-background {
    transform: rotate(-75deg) translateY(100%);
  }
}

@media (min-width: 1200px) {
  .banner .banner-background {
    transform: rotate(-75deg) translateY(113%);
  }
}

@media (min-width: 992px) {
  .banner .banner-box .banner-box-image {
    margin: 30px 30px 0 0;
  }
}

@media (min-width: 992px) {
  .banner .banner-box .banner-box-text h2 {
    text-align: right;
  }
}

@media (min-width: 992px) {
  .banner .banner-box .banner-box-text p {
    padding: 0 0 0 100px;
    text-align: right;
  }
}

.banner .banner-box .banner-box-text .banner-box-text-link a svg {
  margin-right: -15px;
  margin-left: 0;
}

.banner .banner-box .banner-box-text .banner-box-text-link a:hover svg {
  margin-right: 10px;
  margin-left: 0;
}

