/* Start Style the About Information Section*/
.about-info .about-boxs .about-left-boxs .about-left-box a:hover svg {
  margin-right: 10px;
  margin-left: 0;
}

.about-info .about-boxs .about-left-boxs .about-left-box a svg {
  margin-right: -15px;
  margin-left: 0;
}

.about-info .about-boxs .about-right-boxs .about-right-box .about-right-box-texts .about-right-box-text:first-of-type {
  margin-right: 0;
  margin-left: 20px;
}

/* Finish Style the About Information Section*/
/* Start Style the Experience and Education Section*/
.experience-and-education .experience-and-education-boxs .experience-and-education-boxs-boxs .experience-and-education-boxs-box .experience-and-education-boxs-box-icon::after {
  right: 20px;
  left: auto;
}

/* Finish Style the Experience and Education Section*/
/* Finish Style the About Page*/
/* Start Style the Contact Page*/
/* Start Style the Contact Information Section*/
.contact-info .contact-boxs .contact-left-box .contact-left-box-icons .contact-left-box-icon .contact-left-box-icon-icon {
  margin-left: 15px;
  margin-right: 0;
}

.contact-info .contact-boxs .contact-right-box form input::placeholder {
  text-align: right;
}

.contact-info .contact-boxs .contact-right-box form input:last-of-type {
  margin: 0;
}

@media (min-width: 768px) {
  .contact-info .contact-boxs .contact-right-box form input {
    margin: 0 0 0 5%;
  }
}

@media (min-width: 992px) {
  .contact-info .contact-boxs .contact-right-box form input {
    margin: 0 0 0 2%;
  }
}

.contact-info .contact-boxs .contact-right-box form textarea::placeholder {
  text-align: right;
}

.contact-info .contact-boxs .contact-right-box form button:hover svg {
  margin-right: 10px;
  margin-left: 0;
}

.contact-info .contact-boxs .contact-right-box form button svg {
  margin-left: 0;
  margin-right: -15px;
}

